/* google font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&family=VT323&display=swap');

/* layout */
:root {
  --primary: #0668ca;
  --primaryy: #28e6b0;
  --btn: #9DDAEE;
  --h4:#4A81EC;
  --error: #e7195a;
}
body {
  background: #f1f1f1;
  margin: 0;
  font-family: "Poppins";
  
}
.pages{
  
  max-width: 1400px;
  padding: 20px;
  margin: 0 auto;
}

#tfrom{
  background: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
}

/* ticket from */
#ticket-from {
  max-width: 1400px;
  margin: 0 auto;
  max-width: 1400px;
  display: flex;
  font-family: "Poppins";
  border: 2px solid #1d78d3;
  border-radius: 5px;
  /* box-shadow: 1px 3px 10px #e9ecef; */
  background-color: white;
}

#ticket-from span {
  color: #adb5bd;
}

#ticket-from #col-1 {
  margin: auto;
  padding: 50px;
  width: 680px;
}

#tfrom label, input {
  display: block;
}

#tfrom input {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #330fff;
  border-radius: 4px;
  box-sizing: border-box;
}

#tfrom select {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

#tfrom button {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 10px;
  font-family: "Poppins";
  border-radius: 4px;
  cursor: pointer;
}

#tfrom div.error {
  padding: 10px;
  background: #ffefef;
  border: 1px solid var(--error);
  color: var(--error);
  border-radius: 4px;
  margin: 20px 0;
}

#tfrom input.error {
  border: 1px solid var(--error);
}


#ticket-from #col-2 img {
  width: 680px;
  height: 100%;
  object-fit: cover;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  display: none;
}

@media screen and (min-width: 860px) {
  #ticket-from #col-2 img {
    display: initial;
  }
}


/*details show*/

#ticket-details {
  max-width: 1400px;
  margin: 0 auto;
  background: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.05);
}

#ticket-response {
  background: #3b79d660;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.05);
}

#ticket-details h4 {
  margin: 0 0 10px 0;
  font-size: 1.2em;
  color: var(--h4);
}

#ticket-details p {
  margin: 8px;
  font-size: 0.9em;
  color: #555;
}

#ticket-details button {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 11px;
  margin-left: 10px;
  font-family: "Poppins";
  border-radius: 4px;
  cursor: pointer;
}

#ticket-details button:hover {
  background-color: #9DDAEE;
}

#search-section{
  max-width: 1400px;
  margin: 0 auto;
}
#search-section input {
  
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 50%;
  border: 1px solid #ddd;
  border-radius: 50px;
  box-sizing: border-box;
}

#search-section button {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 11px;
  margin-left: 10px;
  font-family: "Poppins";
  border-radius: 4px;
  cursor: pointer;
}

#ticket-r{
  background: #3b79d660;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.05);
}

#message-container {
  background-color: #f1f1f1;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}

#message-container p {
  margin: 10px 0;
}

#reference-number {
  font-weight: bold;
  color: var(--primary);
}

#team-contact {
  font-weight: bold;
  color: #333;
}